@import url('css/variables.css');

.nav {
  display: none;
}

@media screen and (min-width: 1440px) {
  .nav {
    display: flex;
  }

  .navLink {
    border-radius: 40px;
    border: 1px solid var(--nav-link-border);
    margin-right: 16px;
    padding: 8px 16px;

    /* color: #161F37; */
    color: var(--main-button-bg);
    font-size: 14px;
    font-weight: 500;
    line-height: calc(23 / 14);
    transition: color 0.4s, background-color 0.4s;
  }

  .navLink:hover,
  .navLink:focus {
    color: #f3f3f3;
    background-color: #161f37;
  }

  .navLink:last-child {
    margin-right: 0;
  }
}
